const postData = async (url, data) => {
    let res = await fetch(url, {
        method: "POST",
        headers: {
            'Content-type': 'aplication/json; charset=utf-8'
            },
        body: data 
    });
    return await res.json();
};

const getResource = async (url, ) => {
    let res = await fetch(url);

    if (!res.ok) { 
        throw new Error(`Could not fetch ${url}, status: ${res.status}`);
    }
    
    return await res.json();
};

export {postData};
export {getResource};